'use strict';

/**
* Compute the minimum absolute value of a strided array.
*
* @module @stdlib/stats-base-minabs
*
* @example
* import minabs from '@stdlib/stats-base-minabs';
*
* var x = [ 1.0, -2.0, 2.0 ];
*
* var v = minabs( x.length, x, 1 );
* // returns 1.0
*
* @example
* import minabs from '@stdlib/stats-base-minabs';
*
* var x = [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ];
*
* var v = minabs.ndarray( 4, x, 2, 1 );
* // returns 1.0
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;

// exports: { "ndarray": "main.ndarray" }
